import { Fragment, useRef } from "react";
import { Dialog, Transition } from "@headlessui/react";
import { ErrorMessage, Field, Form, Formik } from "formik";
import * as yup from "yup";

const ModalOperationForm = ({ ...props }) => {
  const { newOperationItem, onSubmit, isEdit, isOpen, handleCloseModal } =
    props;
  const cancelButtonRef = useRef(null);

  const OperationSchema = yup.object().shape({
    concept: yup.string().required(),
    category: yup.string().required(),
    type: yup.string().required(),
    amount: yup.number().moreThan(-1, "Amount must be a positive number").required(),
    date: yup.date(),
  });

  return (
    <Formik
      initialValues={newOperationItem}
      validationSchema={OperationSchema}
      onSubmit={onSubmit}
      enableReinitialize={true}
    >
      {({ isSubmitting }) => (
        <Transition.Root show={isOpen} as={Fragment}>
          <Dialog
            as="div"
            className="fixed z-10 inset-0 overflow-y-auto"
            initialFocus={cancelButtonRef}
            onClose={() => {}}
          >
            <div className="flex items-end justify-center min-h-screen pt-4 px-4 pb-40 text-center sm:block sm:p-0">
              <Transition.Child
                as={Fragment}
                enter="ease-out duration-300"
                enterFrom="opacity-0"
                enterTo="opacity-100"
                leave="ease-in duration-200"
                leaveFrom="opacity-100"
                leaveTo="opacity-0"
              >
                <Dialog.Overlay className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity" />
              </Transition.Child>
              <Transition.Child
                as={Fragment}
                enter="ease-out duration-300"
                enterFrom="opacity-0 translate-y-4 sm:translate-y-0 sm:scale-95"
                enterTo="opacity-100 translate-y-0 sm:scale-100"
                leave="ease-in duration-200"
                leaveFrom="opacity-100 translate-y-0 sm:scale-100"
                leaveTo="opacity-0 translate-y-4 sm:translate-y-0 sm:scale-95"
              >
                <Form className="sm:px-10 px-5 py-5 inline-flex flex-col align-bottom bg-gradient-to-br from-indigo-400 to-indigo-500 rounded-lg text-left overflow-hidden shadow-xl sm:translate-y-40 transform transition-all sm:my-5 sm:max-w-lg">
                  <h1 className="mx-auto mb-4 text-xl text-white text-center">
                    {!isEdit ? (
                      <>{"Please insert a new Operation"}</>
                    ) : (
                      <>{"Please update the Operation"}</>
                    )}
                  </h1>
                  <label className="text-white">Concept: </label>
                  <Field
                    placeholder="Example: Youtube Premium"
                    className="h-8 focus:ring-indigo-600 focus:border-indigo-600 focus:outline-none border block w-full px-1 sm:text-lg border-gray-300"
                    name="concept"
                  />
                  <ErrorMessage
                    component={({ children }) => (
                      <p className=" text-red-500 text-sm capitalize">
                        {children}
                      </p>
                    )}
                    name="concept"
                  />
                  <label className="text-white">Category: </label>
                  {newOperationItem.type === "Income" ? (
                    <Field
                      as={"select"}
                      className="h-8 focus:ring-indigo-600 focus:border-indigo-600 focus:outline-none border block w-full px-1 sm:text-lg border-gray-300"
                      name="category"
                    >
                      <option value="Crypto">Crypto</option>
                      <option value="Profit">Profit</option>
                      <option value="Inheritance">Inheritance</option>
                      <option value="Salary">Salary</option>
                      <option value="Sale">Sale</option>
                      <option value="Rental">Rental</option>
                      <option value="Transaction">Transaction</option>
                      <option value="Other">Other</option>
                    </Field>
                  ) : (
                    <Field
                      className="h-9 focus:ring-indigo-600 focus:border-indigo-600 focus:outline-none border block w-full px-1 sm:text-lg border-gray-300"
                      name="category"
                      as={"select"}
                    >
                      <option value="Crypto">Crypto</option>
                      <option value="Food">Food</option>
                      <option value="School">School</option>
                      <option value="University">University</option>
                      <option value="Internet">Internet</option>
                      <option value="Credit Card">Credit Card</option>
                      <option value="Rental">Rental</option>
                      <option value="Transaction">Transaction</option>
                      <option value="Services">Services</option>
                      <option value="Other">Other</option>
                    </Field>
                  )}
                  <ErrorMessage
                    component={({ children }) => (
                      <p className=" text-red-500 text-sm capitalize">
                        {children}
                      </p>
                    )}
                    name="category"
                  />
                  <label className="text-white">Amount: </label>
                  <Field
                    className="h-8 focus:ring-indigo-600 focus:border-indigo-600 focus:outline-none border block w-full px-1 sm:text-lg border-gray-300"
                    type="number"
                    name="amount"
                  />
                  <ErrorMessage
                    component={({ children }) => (
                      <p className=" text-red-500 text-sm capitalize">
                        {children}
                      </p>
                    )}
                    name="amount"
                  />

                  <label className="text-white">Type: </label>
                  <Field
                    className="h-8 focus:ring-indigo-600 focus:border-indigo-600 focus:outline-none border block w-full px-1 sm:text-lg border-gray-300"
                    defaultValue={newOperationItem.type}
                    name="type"
                    as={"select"}
                    disabled={isEdit}
                  >
                    <option value="Income">Income</option>
                    <option value="Expense">Expense</option>
                  </Field>
                  <ErrorMessage
                    component={({ children }) => (
                      <p className=" text-red-500 text-sm capitalize">
                        {children}
                      </p>
                    )}
                    name="type"
                  />

                  <label className="text-white">Date: </label>
                  <Field
                    className="h-8 focus:ring-indigo-600 focus:border-indigo-600 focus:outline-none border block w-full px-1 sm:text-lg border-gray-300 "
                    type="date"
                    name="date"
                  />
                  <ErrorMessage
                    component={({ children }) => (
                      <p className=" text-red-500 text-sm capitalize">
                        {children}
                      </p>
                    )}
                    name="date"
                  />
                  <div className="flex justify-around text-white mt-3">
                    <button
                      className={`w-20  ${
                        !isEdit
                          ? "bg-green-500 hover:bg-green-600"
                          : "bg-indigo-400 hover:bg-indigo-600"
                      } rounded-md px-4 py-1 transform hover:scale-105 duration-300 ease-in-out ${
                        isSubmitting
                          ? "cursor-not-allowed opacity-80"
                          : " cursor-pointer"
                      }`}
                      type="submit"
                      disabled={isSubmitting}
                    >
                      {isEdit ? <>{"Update"}</> : <>{"Add"}</>}
                    </button>
                    <button
                      className=" w-20 bg-red-500 hover:bg-red-600 rounded-md px-4 py-1 transform hover:scale-105 duration-300 ease-in-out"
                      type="button"
                      ref={cancelButtonRef}
                      onClick={handleCloseModal}
                    >
                      Close
                    </button>
                  </div>
                </Form>
              </Transition.Child>
            </div>
          </Dialog>
        </Transition.Root>
      )}
    </Formik>
  );
};

export default ModalOperationForm;
