import { UserIcon } from "@heroicons/react/solid";
import React, { useContext, useEffect, useState } from "react";
import { AuthContext } from "../../AuthContext";
import CenterWhiteContainer from "../../components/Containers/CenterWhiteContainer";
import Dropdown from "../../components/Features/DropDown";
import ModalWindow from "../../components/Modals/ModalWindow";
import userService from "../../services/user";

const Profile = () => {
  const [user, setUser] = useState({});
  const [isDisabled, setIsDisabled] = useState(true);
  const [openDeleteModal, setOpenDeleteModal] = useState(false);
  const [openConfirmModal, setOpenConfirmModal] = useState(false);
  const { signout, setUserData } = useContext(AuthContext);

  const handleClose = () => {
    setOpenDeleteModal(false);
  };

  const handleDelete = () => {
    userService.deleteMe().then(() => {
      signout();
      setOpenDeleteModal(false);
    });
  };

  useEffect(() => {
    userService.getMe().then((res) => {
      setUser(res?.data);
    });
  }, []);

  const handleSubmit = (e) => {
    e.preventDefault();
    if (!isDisabled) {
      userService.updateMe(user).then(() => {
        setOpenConfirmModal(true);
        setIsDisabled(true)
        setUserData(user)
      });
    }
  };

  const handleChange = (e) => {
    setUser({
      ...user,
      [e.target.name]: e.target.value,
    });
  };

  return (
    <CenterWhiteContainer>
    <form className=" grid relative " onSubmit={(e) => handleSubmit(e)}>
      <div className=" absolute top-0 right-0 sm:hidden z-20">
        <Dropdown
          onEdit={() => {
            setTimeout(()=>{setIsDisabled(false)},500);
          }}
          onDelete={() => {
            setOpenDeleteModal(true);
          }}
        />
      </div>
      <h1 className=" text-center sm:text-3xl text-xl">
        <strong className=" text-indigo-600">My Profile</strong>
      </h1>
      <div className=" grid grid-cols-2 my-4">
        <div className="grid justify-center items-center">
          <div className=" sm:w-72 sm:h-72 w-32  rounded-full">
            <UserIcon className=" text-indigo-900" />
          </div>
        </div>
        <div className=" grid items-center max-w-prose text-xs sm:text-base gap-1">
          <div>
            <label>Name:</label>
            <input
              disabled={isDisabled}
              value={user?.name}
              name="name"
              className={`sm:h-8 bg-indigo-100 shadow-sm focus:ring-indigo-600 focus:border-indigo-600 focus:outline-none border block w-full px-1 sm:text-lg border-gray-300 ${isDisabled && "opacity-70"}`}
              type="text"
              onChange={handleChange}
            />
          </div>
          <div>
            <label>Surname:</label>
            <input
              disabled={isDisabled}
              value={user?.surname}
              name="surname"
              className={`sm:h-8 bg-indigo-100 shadow-sm focus:ring-indigo-600 focus:border-indigo-600 focus:outline-none border block w-full px-1 sm:text-lg border-gray-300 ${isDisabled && "opacity-70"}`}
              type="text"
              onChange={handleChange}
            />
          </div>
          <div>
            <label>Username:</label>
            <input
              disabled={isDisabled}
              value={user?.username}
              name="username"
              className={`sm:h-8 bg-indigo-100 shadow-sm focus:ring-indigo-600 focus:border-indigo-600 focus:outline-none border block w-full px-1 sm:text-lg border-gray-300 ${isDisabled && "opacity-70"}`}
              type="text"
              onChange={handleChange}
            />
          </div>
        </div>
      </div>
      <div className=" sm:flex justify-around hidden text-white">
        <button
          onClick={() => {
            setTimeout(()=>{setIsDisabled(false)},500);
          }}
          className="w-20 bg-indigo-600 hover:bg-indigo-900 shadow-sm rounded-md px-4 py-1 transform hover:scale-105 duration-300 ease-in-out"
          type="submit"
        >
          Edit
        </button>
        <button
          className="w-20 bg-red-500 hover:bg-red-700 shadow-sm rounded-md px-4 py-1 transform hover:scale-105 duration-300 ease-in-out"
          onClick={() => {
            setOpenDeleteModal(true);
          }}
          type="button"
        >
          Delete
        </button>
      </div>
      <ModalWindow
        modalText={"Are you sure you want to delete your account?"}
        open={openDeleteModal}
        handleClose={() => handleClose()}
        handleConfirm={() => handleDelete()}
        type={"delete"}
      />
      <ModalWindow
        modalText={"Your account has been updated successfully"}
        open={openConfirmModal}
        handleConfirm={() => setOpenConfirmModal(false)}
        type={"success"}
      />
    </form>
    </CenterWhiteContainer>
  );
};

export default Profile;
