import { Transition } from '@headlessui/react';
import React from 'react'

const CenterWhiteContainer = ({children}) => {
    return ( 
        
        <Transition
        as="div"
        className={"max-w-max text-gray-900 bg-gray-50 rounded-lg shadow-lg p-5 m-auto"}
        appear={true}
        show={true}
        enter="ease-in-out duration-500"
        enterFrom=" transform opacity-20 translate-y-10"
        enterTo=" transform"
        leave="ease-out duration-1000"
        leaveFrom=" transform opacity-100"
        leaveTo=" transform opacity-50"
      >
      {children}
      </Transition>
   
     );
}
 
export default CenterWhiteContainer;