import { LockClosedIcon, CashIcon } from '@heroicons/react/solid'
import { ErrorMessage, Field, Form, Formik } from 'formik';
import { Link } from 'react-router-dom';
import * as yup from "yup"

const FormUserRegister = ( {...props}) => {
  const {onSubmit, error}  = props
  const initialValues = {
    name: "",
    surname: "",
    username: "",
    password: "",
  };

  const registerSchema = yup.object().shape({
    name: yup.string().required("The name is required"),
    surname: yup.string().required("The surname is required"),
    username: yup.string().required("The username is required"),
    password: yup
      .string()
      .min(4, "The password must be at least 4 characters long")
      .required("The password is required"),
  });

    return (    
    <>
      <div className="min-h-full flex items-center justify-center py-5  px-4 sm:px-6 lg:px-8">
        <div className="max-w-md w-full space-y-8">
          <div className='text-center'>
            <CashIcon className="  sm:h-48 h-32 m-auto text-indigo-600"/>
            <h2 className="mt-6 text-center text-3xl font-extrabold text-gray-900">Create your account</h2>
            
          </div>
          <Formik
            initialValues={initialValues}
            validationSchema={registerSchema}
            onSubmit={onSubmit}
          >
          <Form className="mt-8 space-y-6" >
            {error && <div className=' bg-red-500 text-center p-2 text-white'>{error}</div>}
            <input type="hidden" name="remember" defaultValue="true" />
            <div className="rounded-md shadow-sm -space-y-px">
                <div>
                <label className="sr-only">
                  Name
                </label>
                <Field
                  name="name"
                  className="appearance-none rounded-none relative block w-full px-3 py-2 border border-gray-300 placeholder-gray-500 text-gray-900 rounded-t-md focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 focus:z-10 sm:text-sm"
                  placeholder="Name"
                />
                <ErrorMessage component={({children})=><p className=" text-red-500 text-sm">{children}</p>} name="name" /> 
              </div>
              <div>
                <label className="sr-only">
                  Surname
                </label>
                <Field
                  name="surname"
                  className="appearance-none rounded-none relative block w-full px-3 py-2 border border-gray-300 placeholder-gray-500 text-gray-900 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 focus:z-10 sm:text-sm"
                  placeholder="Surname"
                />
                <ErrorMessage component={({children})=><p className=" text-red-500 text-sm">{children}</p>} name="surname" /> 
              </div>
              <div>
                <label className="sr-only">
                  Username
                </label>
                <Field
                  name="username"
                  className="appearance-none rounded-none relative block w-full px-3 py-2 border border-gray-300 placeholder-gray-500 text-gray-900 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 focus:z-10 sm:text-sm"
                  placeholder="Username"
                />
                <ErrorMessage component={({children})=><p className=" text-red-500 text-sm">{children}</p>} name="username" /> 
              </div>
              <div>
                <label className="sr-only">
                  Password
                </label>
                <Field
                  name="password"
                  type="password"
                  className="appearance-none rounded-none relative block w-full px-3 py-2 border border-gray-300 placeholder-gray-500 text-gray-900 rounded-b-md focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 focus:z-10 sm:text-sm"
                  placeholder="Password"
                />
                <ErrorMessage component={({children})=><p className=" text-red-500 text-sm">{children}</p>} name="password" /> 
              </div>
            </div>

            <div>
              <button
                type="submit"
                className="group relative w-full flex justify-center py-2 px-4 border border-transparent text-sm font-medium rounded-md text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
              >
                <span className="absolute left-0 inset-y-0 flex items-center pl-3">
                  <LockClosedIcon className="h-5 w-5 text-indigo-500 group-hover:text-indigo-400" aria-hidden="true" />
                </span>
                Sign up
              </button>
              <div className="text-lg text-center m-2">
                 Already have an account?{" "}
                <Link to="/login" className=" text-lg text-indigo-600 hover:text-indigo-500">
                   Log In
                </Link>
              </div>
            </div>
          </Form>
          </Formik>
        </div>
      </div>
    </>
    )
}

export default FormUserRegister;