import axios from "axios";
let APIURL;
if (process.env.NODE_ENV === "development") {
  APIURL = "http://localhost:4000";
} else {
  APIURL = "https://alkemy-back-challenge.herokuapp.com";
}
const baseURL = `${APIURL}/api/users/me`;

const getMe = async () => {
  const config = { headers: { authorization: localStorage.getItem("token") } };
  try {
    const { data } = await axios.get(baseURL, config);
    return data;
  } catch (error) {
    console.log(error);
  }
};

const deleteMe = async () => {
  const config = { headers: { authorization: localStorage.getItem("token") } };
  try {
    const { data } = await axios.delete(baseURL, config);
    return data;
  } catch (error) {
    console.log(error);
  }
};

const updateMe = async (user) => {
  const config = { headers: { authorization: localStorage.getItem("token") } };
  try {
    const { data } = await axios.patch(baseURL, user, config);
    return data;
  } catch (error) {
    console.log(error);
  }
};

const userService = { getMe, deleteMe, updateMe };

export default userService;
