import React from "react";
import ItemHeader from "../ItemHeader";

function SortHeader({ handleResetSort, handleToggleSort, toggleSort }) {
  return (
    <div className="text-xs text-indigo-900 font-light flex flex-row flex-wrap gap-2 py-3 sm:hidden">
        <div
          className="text-left "
        >
         <ItemHeader
            handleResetSort={handleResetSort}
            handleToggleSort={handleToggleSort}
            toggleSort={toggleSort}
            name={"concept"}
          />
        </div>
        <div
          className="text-left  "
        >
          <ItemHeader
            handleResetSort={handleResetSort}
            handleToggleSort={handleToggleSort}
            toggleSort={toggleSort}
            name={"category"}
          />
        </div>
        <div
          className="text-left  "
        >
          <ItemHeader
            handleResetSort={handleResetSort}
            handleToggleSort={handleToggleSort}
            toggleSort={toggleSort}
            name={"amount"}
          />
        </div>
        <div
          className="text-left   "
        >
          <ItemHeader
            handleResetSort={handleResetSort}
            handleToggleSort={handleToggleSort}
            toggleSort={toggleSort}
            name={"type"}
          />
        </div>
        <div
          className="text-left  "
        >
          <ItemHeader
            handleResetSort={handleResetSort}
            handleToggleSort={handleToggleSort}
            toggleSort={toggleSort}
            name={"date"}
          />
        </div>
      </div>
  );
}

export default SortHeader;