import { Transition } from '@headlessui/react';
import React, { useState } from 'react'
import IncomeIcon from "../../../assets/output-onlinepngtools (1).png";
import ExpenseIcon from "../../../assets/output-onlinepngtools.png";
import Dropdown from '../../Features/DropDown';
import ModalWindow from '../../Modals/ModalWindow';

function OperationsCards({ operationList, onEdit, onDelete }) {
    const [openDeleteModal, setOpenDeleteModal] = useState(false);
    const [id, setId] = useState(null);
  
    const handleClose = () => {
      setOpenDeleteModal(false);
    };
  
    const handleDelete = (id) => {
      onDelete(id);
      setOpenDeleteModal(false);
    };
  
    return (
      <div className=" grid gap-2 sm:hidden text-indigo-800">
        {operationList?.map((operation, count) => (
          <Transition
          as="div"
          appear={true}
          show={true}
          enter="ease-in-out duration-500"
          enterFrom=" transform opacity-20 translate-x-10"
          enterTo=" transform"
          leave="ease-out duration-1000"
          leaveFrom=" transform opacity-100"
          leaveTo=" transform opacity-50"
        >
          <div
            key={operation.id}
            className={`${
              count % 2 === 0 ? "bg-indigo-200 " : "bg-indigo-300 "
            } cursor-pointer rounded-2xl p-1 relative border-2 border-indigo-600 shadow-md hover:scale-105 transform duration-300 ease-in-out`}
          >
            <div className=" absolute right-2 top-1 ">
              <Dropdown
                onEdit={() => onEdit(operation)}
                onDelete={() => {
                  setOpenDeleteModal(true);
                  setId(operation.id);
                }}
              />
            </div>
            <div className=" flex">
              <div className=" min-h-full max-w-xs grid justify-center items-centen">
                <div className="p-1 rounded-md  w-24">
                  {operation["type"] === "Expense" ? (
                    <img
                      src={ExpenseIcon}
                      alt=""
                      className=" text-red-500 h-24"
                    />
                  ) : (
                    <img
                      src={IncomeIcon}
                      alt=""
                      className=" text-green-500 h-24"
                    />
                  )}
                </div>
              </div>
              <div className=" text-xs ">
                <p className="px-3 sm:px-6 text-base">
                  {operation.concept}
                </p>
                <p className="px-3 sm:px-6 text-sm">
                 
                  {operation.category}
                </p>
                <p className="px-3 sm:px-6">
                  
                  {operation.date}
                </p>
              </div>
              <div className="flex items-center justify-center w-full text-xl">
                {operation["type"] === "Expense" ? (
                  <strong className=" text-red-500">
                    {"-$"}
                    {operation.amount}
                  </strong>
                ) : (
                  <strong className=" text-green-500">
                    {"+$"}
                    {operation.amount}
                  </strong>
                )}
              </div>
              </div>
            </div>
          </Transition>
        ))}
        <ModalWindow
          modalText={"Are you sure you want to delete this operation?"}
          open={openDeleteModal}
          handleClose={() => handleClose()}
          handleConfirm={() => handleDelete(id)}
          type={"delete"}
        />
      </div>
    );
  }

  export default OperationsCards